@import '~assets/styles/h3Imports';

$header-user-font-size: 12;

.upcoming-task {
  background-color: var(--white);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  display: flex;
  flex-direction: column;
  margin-bottom: var(--spacing-xs);
  padding: var(--spacing-xs) var(--spacing-xs) var(--spacing-xxs);
  width: 100%;
}

.header {
  display: flex;
  margin-bottom: rem(16);
}

.header-user {
  align-items: center;
  color: var(--brand-secondary);
  display: flex;
  font-size: rem($header-user-font-size);
  font-weight: 700;
  letter-spacing: em(0.5, $header-user-font-size);
  margin-left: auto;
  text-align: right;
}

.header-user-icon {
  $icon-size: 20;

  display: flex;
  fill: currentColor;
  flex: 0 0 em($icon-size, $header-user-font-size);
  height: em($icon-size, $header-user-font-size);
  margin-left: em(6, $header-user-font-size);
  width: em($icon-size, $header-user-font-size);
}

.title {
  @include h5;

  margin-bottom: em(8);
  margin-top: 0;
}

.figure {
  --icon-stroke: var(--svg-background-stroke);

  span {
    display: flex;
  }
}

.buttons {
  margin-bottom: -0.2rem;
  margin-left: -0.5rem;
  margin-top: auto;
}
