@import '~assets/styles/h3Imports';

.card-grid-wrap {
  display: flex;
  flex-flow: column;
  margin: 0;
  padding: 0;

  > li {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    margin-bottom: var(--spacing-s);
    margin-top: 0;

    &::before {
      content: '\200B';
      position: absolute;
    }

    > * {
      height: 100%;
      margin-bottom: 0;
    }
  }

  //640px
  @media (min-width: 40em) {
    flex-flow: wrap;

    > * {
      flex: 0 1 calc(50% - 0.75rem);
      margin-right: var(--spacing-s);

      &:nth-child(even) {
        margin-right: 0;
      }
    }
  }

  @media #{$large-up} {
    > * {
      flex: 0 1 calc(33.3% - ((var(--spacing-s) * 2) / 3));
      max-width: calc(33.3% - ((var(--spacing-s) * 2) / 3));

      &:nth-child(even) {
        margin-right: var(--spacing-s);
      }

      &:nth-child(3n) {
        margin-right: 0;
      }
    }

    html[data-fontsize='AAA'] & {
      > * {
        flex: 0 1 calc(50% - 0.75rem);
        max-width: calc(50% - 0.75rem);

        &:nth-child(even) {
          margin-right: 0;
        }

        &:nth-child(3n) {
          margin-right: var(--spacing-s);
        }
      }
    }
  }
}

.card-grid-header {
  @include h3;

  [data-element='notification-counter'] {
    display: inline-block;
    margin-left: var(--spacing-xxxs);
    position: relative;
    top: 5px;
  }
}
